import { useParams } from 'react-router-dom';
import collageapi from 'api/collage';
// import helpers from 'components/collage/helpers';
import BaseCollage from 'pages/collage/Base';

async function downloadImages(imgURLS) {
  const urlFileMap = {};
  console.time('downloading images');
  await Promise.allSettled(
    Object.entries(imgURLS).map(([title, url]) =>
      fetch(url)
        .then((resp) => resp.blob())
        .then((f) => {
          urlFileMap[title] = f;
        })
    )
  );

  return urlFileMap;
}

async function getImages(imgDets, urls, incrementImageCount, startLoad) {
  const urlFileMap = await downloadImages(urls);
  startLoad();
  const tempImages = await Promise.allSettled(
    imgDets.map((img) => {
      const result = new Promise((resolve) => {
        const { naturalWidth, naturalHeight, title, aspectRatio, type } = img;
        const file = urlFileMap[title];
        const image = new Image();
        image.onload = (e) => {
          const { src } = e.target;
          const imageDetail = {
            title,
            src,
            naturalHeight,
            naturalWidth,
            aspectRatio,
            type,
            file,
          };
          // handleImageDetails(imageDetail);
          incrementImageCount();
          resolve(imageDetail);
        };
        image.src = URL.createObjectURL(file);
      });
      return result;
    })
  );
  return tempImages.map((elem) => elem.value);
}

export default function CollageView() {
  const params = useParams();
  const { collageID } = params;

  const getCollage = async (handleImageCount, incrementImageCount) => {
    const details = await collageapi.getCollage(collageID);
    const images = await getImages(
      details.details.images,
      details.urls,
      incrementImageCount,
      () => {
        handleImageCount(details.details.images.length);
      }
    );

    const imageDets = images.map((img) => {
      const { src, file, ...rest } = img;
      return rest;
    });
    return { details: { ...details.details, images: imageDets }, images };
  };

  return (
    <BaseCollage
      getCollage={getCollage}
      clearCollage={() => collageapi.deleteCollage(collageID)}
      saveType="existing"
    />
  );
}
